import React from "react";
import { NavLink } from "react-router-dom";
import { LuArrowRightFromLine, LuArrowLeftFromLine } from "react-icons/lu";
import { IoMdClose } from "react-icons/io";
import { FaTachometerAlt, FaBoxOpen, FaSignOutAlt } from "react-icons/fa";
import { useAuth } from "../../contexts/AuthContext";

const navItems = [
  {
    name: "Dashboard",
    path: "/shipper/dashboard",
    icon: <FaTachometerAlt />,
  },
  {
    name: "Orders",
    path: "/shipper/orders",
    icon: <FaBoxOpen />,
    subPaths: [
      { name: "Pending", path: "/shipper/orders/pending" },
      { name: "Completed", path: "/shipper/orders/completed" },
    ],
  },
];

const Sidebar = ({ mobileOpen, setMobileOpen, isOpen, setIsOpen }) => {
  const { logout } = useAuth();

  const isActivePath = (path, subPaths) => {
    if (window.location.pathname === path) return true;
    if (subPaths)
      return subPaths.some((sub) => sub.path === window.location.pathname);
    return false;
  };

  return (
    <>
      {/* Sidebar Container */}
      <div
        className={`fixed top-0 left-0 z-50 h-full bg-white shadow-lg transition-all duration-300
          md:relative md:translate-x-0 ${isOpen ? "w-64" : "w-20"}
          ${mobileOpen ? "translate-x-0 w-full" : "-translate-x-full"}
        `}
      >
        {/* Close button for mobile */}
        <div className="flex justify-end p-4 md:hidden">
          <button onClick={() => setMobileOpen(false)}>
            <IoMdClose size={28} />
          </button>
        </div>

        {/* Desktop toggle */}
        <div className="hidden md:flex justify-end p-2">
          <button
            className="text-system-primary hover:text-accent transition-colors"
            onClick={() => setIsOpen(!isOpen)}
          >
            {isOpen ? (
              <LuArrowLeftFromLine size={20} />
            ) : (
              <LuArrowRightFromLine size={20} />
            )}
          </button>
        </div>

        {/* Navigation */}
        <nav className="flex-1 mt-4 overflow-y-auto min-h-[calc(100vh-80px)]">
          <ul className="space-y-2 px-2">
            {navItems.map((item) => {
              const active = isActivePath(item.path, item.subPaths);
              return (
                <li key={item.path}>
                  <NavLink
                    to={item.path}
                    onClick={() => mobileOpen && setMobileOpen(false)}
                    className={`flex items-center gap-3 px-4 py-2 rounded transition-colors duration-300 hover:bg-gray-100 ${
                      active
                        ? "bg-gray-100 font-semibold text-system-primary"
                        : "text-gray-700"
                    }`}
                  >
                    <span className="text-lg">{item.icon}</span>
                    {isOpen || mobileOpen ? <span>{item.name}</span> : null}
                  </NavLink>

                  {/* Subpaths */}
                  {item.subPaths && (isOpen || mobileOpen) && (
                    <ul className="ml-8 mt-1 space-y-1">
                      {item.subPaths.map((sub) => {
                        const subActive = window.location.pathname === sub.path;
                        return (
                          <li key={sub.path}>
                            <NavLink
                              to={sub.path}
                              onClick={() => mobileOpen && setMobileOpen(false)}
                              className={`block px-4 py-2 rounded transition-colors duration-300 hover:bg-gray-100 ${
                                subActive
                                  ? "bg-gray-100 font-semibold text-system-primary"
                                  : "text-gray-700"
                              }`}
                            >
                              {sub.name}
                            </NavLink>
                          </li>
                        );
                      })}
                    </ul>
                  )}
                </li>
              );
            })}

            {/* Logout */}
            <li className="mt-4">
              <button
                onClick={logout}
                className="flex items-center gap-3 px-4 py-2 w-full text-left rounded hover:bg-red-100 text-red-600 font-semibold"
              >
                <FaSignOutAlt /> {isOpen || mobileOpen ? "Logout" : ""}
              </button>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
};

export default Sidebar;
